body, input, button, select {
  font-family: 'Karla', sans-serif;
  font-size: 16px;
}

html,
body,
.container {
  height: 100%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.5em;
}

img {
  max-width: 100%;
}

input:focus {
  outline: 1px solid #E64C4C;
}

button {
  cursor: pointer;
  width: 100%;
  background: #E64C4C;
  text-transform: uppercase;
  color: #fff;
  padding: 10px;
  border: none;
}
button:focus {
  outline: 1px dashed #000;
}

select {
  border: 1px solid #dedede;
}
select:focus {
  outline: 1px solid #E64C4C;
}

input[type="text"] {
  width: 100%;
  border: 1px solid #dedede;
  height: 45px;
  padding: 5px;
  line-height: 35px;
}

#error-message {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #d83651;
  color: #fff;
  padding: 10px 15px;
  text-align: center;
}

.map {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.map img {
  max-width: 999999999px;
}
.map-overlay {
  position: absolute;
  z-index: 2;
  width: 300px;
  right: 20px;
  top: 80px;
  display: none;
  max-height: 90%;
  max-height: calc(100% - 160px);
  padding-top: 140px;
}
.map-overlay__month-select, .map-overlay__year-select {
  width: 50%;
  width: calc(50% - 5px);
  float: left;
  margin-top: 10px;
}
.map-overlay__year-select {
  margin-right: 10px;
}
.map-overlay__button {
  margin-top: 10px;
}
.map-overlay::before {
  content: "";
  display: block;
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  background: #fff;
  height: 150px;
}
.map-overlay::after {
  content: "";
  display: block;
  position: absolute;
  top: -20px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px;
  border-color: transparent transparent #fff transparent;
  right: 6px;
}
.map-overlay--show {
  display: flex;
  flex-direction: column;
}
.map-overlay__search {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 10px;
}
.map-overlay__police-data {
  background: #fff;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-in-out;
  width: 100%;
  overflow: auto;
}
.map-overlay__police-data--closing .map-overlay__police-data__section {
  transition-delay: 0s;
}
.map-overlay__police-data__total {
  padding: 10px 5px;
}
.map-overlay__police-data__section {
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
  transition-delay: 0.3s;
  position: relative;
  padding: 5px 0 5px 40px;
  cursor: pointer;
}
.map-overlay__police-data__section span {
  background: #efefef;
  position: absolute;
  left: 10px;
  width: 20px;
  top: 5px;
  bottom: 5px;
  display: block;
}
.map-overlay__police-data--show {
  transform: scaleY(1);
}
.map-overlay__police-data--show .map-overlay__police-data__section {
  opacity: 1;
}
.map-overlay__input-status {
  position: absolute;
  right: 10px;
  height: 45px;
  width: 45px;
  top: 0;
}
.map-overlay-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  background: none;
  border: none;
  width: auto;
  height: 45px;
}
.map-overlay-toggle:focus {
  outline: none;
}
.map-overlay-toggle svg {
  width: 20px;
}
.map-overlay-toggle:hover .map-overlay-toggle__icon--active, .map-overlay-toggle--open .map-overlay-toggle__icon--active {
  display: block;
}
.map-overlay-toggle:hover .map-overlay-toggle__icon--default, .map-overlay-toggle--open .map-overlay-toggle__icon--default {
  display: none;
}
.map-overlay-toggle__icon--active {
  display: none;
  fill: #E64C4C;
}

.alert {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 2;
  background: rgba(0, 0, 0, 0.1);
}
.alert__container {
  position: relative;
  z-index: 2;
  max-width: 400px;
  width: 100%;
  padding: 53px 10px 30px;
  background: #fff;
}
.alert__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: auto;
  padding: 7px 10px;
}

.alert--open .alert {
  display: flex;
}


